display: flex;
  justify-content: center;

  min-height: 100vh;
  flex-wrap: wrap;
 
  flex-direction: row;
}
.name{
  text-align:center;
  color:white;
}
h1{
  font-style: italic
}
.container{
 
  text-align:center;
  color:white;
}
.button{
  /* color:white; */
  
  
}
.avatar{
  height: 100px;
  width: 100px;
  object-fit: cover;

}


.box {
  position: relative;
  width: 300px;
  height: 400px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: #060c21;
  
  margin-top: 80px;
  margin-left: 150px;
}
.box::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: #fff;
  z-index: -1;
  filter: blur(40px);
  
}

.box::after {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: #fff;
  z-index: -2;
  filter: blur(40px);
}



.box::before,
.box::after {
  background: linear-gradient(255deg, #ff005e, #010615, #fbff00);
}


.glass {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  display: block;
  background: rgba(255, 255, 255, 0.1);
  pointer-events: none;
}

.content {
  padding: 20px;
  color: #fff;
}


@media only screen and (max-width: 600px) {
  
.box{
  margin-top: 10px;
  margin-left: 33px;
}
  
}